<template>
    <yd-layout class="box_danger">
        <navBar title="随手拍隐患记录" slot="navbar"></navBar>
        <div v-if="!didLoadData">
            <div class="boxload">
                <div class="boxload_content">
                    <img src="../../assets/icon_loading.png" class="Rotation" >
                    <div>稍等哟~正在加载</div>
                </div>
            </div>
        </div>

        <div v-if="didLoadData && data.length == 0">
            <div class="boxload">
                <div class="boxload_content">
                    <img src="../../assets/icon_lose.png" >
                    <div>哎呀!还没有数据</div>
                </div>
            </div>
        </div>
        <div v-if="didLoadData && data.length > 0">
            <yd-pullrefresh :callback="doRefreshData" ref="listViewRefresh">
                <yd-infinitescroll :callback="doLoadMoreData" ref="listViewLoadMore">
                    <yd-list slot="list">
                        <!--隐患记录-->
                        <div class="div_list" v-for="item,index in data" :key="index">
                            <!--检查点-->
                            <div class="list_top">
                                <p class="list_title" style="color: gray" v-if="item.STATE == 0">待审核</p>
                                <p class="list_title" style="color: red" v-if="item.STATE == 1">待整改</p>
                                <p class="list_title" style="color: orange;" v-if="item.STATE == 2">待复查</p>
                                <p class="list_title" style="color: rgb(29, 145, 230)" v-if="item.STATE == 3">通&nbsp;&nbsp;&nbsp;过</p>
                                <div style="flex: 1;"></div>
                                <p class="list_btn_red" v-if="item.STATE == 1" @click="toCorrect(0,item.ID,item.ZHENGGAI_NAME)">整 改</p>
                                <p class="list_btn_orange" v-if="item.STATE == 2" @click="toCorrect(1,item.ID,item.ZHENGGAI_NAME)">复 查</p>
                            </div>
                            <div class="list_org">
                                <p class="org">检查人</p>
                                <p style="flex: 1;">{{item.JIANCHA_NAME}}</p>
                            </div>
                            <div class="list_org">
                                <p class="org">时&nbsp;&nbsp;&nbsp;间</p>
                                <p style="flex: 1;">{{item.CREATE_AT}}</p>
                            </div>
                            <div class="list_org">
                                <p class="org">公&nbsp;&nbsp;&nbsp;司</p>
                                <p style="flex: 1;">{{item.QIYE_NAME}}</p>
                            </div>
                            <div class="list_org">
                                <p class="org">备&nbsp;&nbsp;&nbsp;注</p>
                                <p style="flex: 1;">{{item.REMARKS}}</p>
                            </div>
                            <!--隐患图片-->
                            <div class="list_org">
                                <p class="org">附&nbsp;&nbsp;&nbsp;件</p>
                                <div class="flex_image">
                                    <div class="flex_image_img" @click="onPressLightbox($event,item.ATTACHMENT)">
                                        <img :src="item.ATTACHMENT" alt="">
                                    </div>
                                </div>
                            </div>
                            <!--整改记录-->
                            <div v-if="item.recordnum">
                                <yd-accordion>
                                    <yd-accordion-item title="整改记录">
                                        <div class="list_record" v-for="items,key in item.history" :key="key">
                                            <div>
                                                <div style="color: #1D91E6;">整改</div>
                                                <div>整改金额：{{items.ZG_MONEY}}元</div>
                                                <div>整改人员：{{item.ZHENGGAI_NAME}}</div>
                                                <div>整改时间：{{items.ZG_TIME}}</div>
                                                <div>整改备注：{{items.ZG_REMARKS}}</div>
                                                <div class="box_img" @click="onPressLightbox($event,items.ZG_ATTACHMENT)">
                                                    <img :src="items.ZG_ATTACHMENT" alt="">
                                                </div>
                                            </div>
                                            <div v-if="items.FC_FINISH">
                                                <div style="color: orange">复查</div>
                                                <div>复查金额：{{items.FC_MONEY}}元</div>
                                                <div>复查人员：{{item.FUCHA_NAME}}</div>
                                                <div>复查时间：{{items.FC_TIME}}</div>
                                                <div>复查备注：{{items.FC_REMARKS}}</div>
                                                <div class="box_img" @click="onPressLightbox($event,items.FC_ATTACHMENT)">
                                                    <img :src="items.FC_ATTACHMENT" alt="">
                                                </div>
                                            </div>
                                        </div>
                                    </yd-accordion-item>
                                </yd-accordion>
                            </div>

                        </div>

                        <!-- 数据全部加载完毕显示 -->
                        <span slot="doneTip">-- 全部加载完成 --</span>

                        <!-- 加载中提示，不指定，将显示默认加载中图标 -->
                        <img slot="loadingTip" src="http://static.ydcss.com/uploads/ydui/loading/loading10.svg"/>
                    </yd-list>
                </yd-infinitescroll>
            </yd-pullrefresh>
        </div>

    </yd-layout>
</template>

<script>
    import {api_camera_list} from '../../components/APIKeys'
    export default {
        name: "CameraList",
        data() {
            return {
                // token: window.localStorage.getItem('token'),
                data: [],
                recordnum: false,//是否显示整改记录
                hasMore: true,//是否有下一页
                page: 1,//页数
                pageSize: 10,//每页个数
                didLoadData:false, //是否加载完数据
            }
        },
        mounted() {
            this.doLoadDanger((res) => {
                this.data = [...res.data.data];
            });
        },
        methods: {
            //获取列表数据
            doLoadDanger(data) {
                // this.$showDialog();
                let jsonData = {
                    // token: this.token,
                    pageIndex: this.page,
                    pageSize: this.pageSize,
                };
                this.$post(api_camera_list, jsonData).then(res => {
                    this.didLoadData = true;
                    // this.$showDialog(false);
                    if (res.errno == 0) {
                        data && data(res);
                        // this.data = res.data.data || [];
                        this.hasMore = res.data.hasMore;
                        for (let i = 0; i < this.data.length; i++) {
                            if (this.data[i].history.length > 0) {
                                this.data[i].recordnum = true;
                            } else {
                                this.data[i].recordnum = false;
                            }
                        }
                    } else {
                        this.$showError(res.errmsg);
                    }
                })
            },
            /**
             * 上拉加载
             */
            doLoadMoreData: function () {
                // console.log('this.hasMore========= ',this.hasMore);
                if (!this.hasMore) {
                    this.$refs.listViewLoadMore.$emit('ydui.infinitescroll.loadedDone');
                    return;
                }
                this.page++;
                let self = this;
                this.doLoadDanger((res) => {
                    self.data = [...self.data, ...res.data.data];
                    // self.hasMore = self.data.length < parseInt(res.total);
                    self.hasMore = res.data.hasMore;

                    if (self.data.length > 0) {
                        // 所有数据加载完成
                        if (!self.hasMore) {
                            self.$refs.listViewLoadMore.$emit('ydui.infinitescroll.loadedDone');
                        }
                        else {
                            /* 单次请求数据完毕 */
                            self.$refs.listViewLoadMore.$emit('ydui.infinitescroll.finishLoad');
                        }
                    }
                })
            },
            /**
             * 下拉刷新
             */
            doRefreshData: function () {
                this.page = 1;
                let self = this;
                this.doLoadDanger((res) => {
                    self.data = [...res.data.data];
                    if (self.$refs.listViewRefresh) {
                        self.$refs.listViewRefresh.$emit('ydui.pullrefresh.finishLoad');
                    }
                });
            },
            //点击放大图片
            onPressLightbox(e, img) {
                let images = [
                    {url: img},
                ];
                this.$showLightbox(images);
                // fancyBox(e.target, images);
            },
            //点击页面跳转
            toCorrect(index,id,name) {
                window.localStorage.setItem("ssp_id",id);
                //去整改
                if (index == 0) {
                    this.$pushRoute('CameraCorrect');
                } else {
                    window.localStorage.setItem("correctname",name);
                    this.$pushRoute('CameraRecheck');
                }
            },
        }
    }
</script>

<style scoped>
    .boxload {
        height: 3.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .boxload_content{
        width: 3rem;
        height:2.7rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .boxload_content img{
        width: 1.54rem;
        height: 1.54rem;
    }
    .boxload_content div{
        color: #cdcdcd;
        margin-top: 0.2rem;
    }
    @-webkit-keyframes rotation{
        from {-webkit-transform: rotate(0deg);}
        to {-webkit-transform: rotate(360deg);}
    }

    .Rotation{
        transform: rotate(360deg);
        animation: rotation 1s linear infinite;
    }
    .div_list {
        margin: .24rem;
        background: #fff;
        padding: .24rem;

    }

    .list_top {
        display: flex;
        align-items: center;
        margin-bottom: 0.2rem;
    }


    .list_title {
        font-size: .34rem;
        font-weight: 600;
    }


    .list_cont_result p {
        font-size: .26rem;
        color: #909090;
    }

    .list_org {
        margin-top: .2rem;
        display: flex;
    }

    .list_org .org {
        color: #909090;
        margin-right: .2rem;
    }

    .list_btn_red {
        padding: 0 .24rem;
        border: 1px solid red;
        color: red;
    }

    .list_btn_orange {
        padding: 0 .24rem;
        border: 1px solid orange;
        color: orange;
    }

    .list_record {
        padding: .24rem;
        color: #101010;
    }

    .box_img {
        width: calc(95% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        margin-bottom: .1rem;
    }

    .box_img img {
        width: 100%;
        height: 100%;
    }
    .flex{
        display: flex;
    }
    .flex_image{
        flex: 1;
        display: flex;
        align-items: center;
    }
    .flex_image_img{
        width: calc(107% / 3);
        height: 1.8rem;
    }
    .flex_image_img img{
        width: 100%;
        height: 100%;
    }
</style>
<style>
    .box_danger .yd-accordion-title-full {
        background: #EEEEEE;
        padding: 0 .24rem;
    }

    .box_danger .yd-accordion-head {
        padding: 0;
    }

    .box_danger .yd-accordion-head-arrow {
        background: #EEEEEE;
        padding-right: .24rem;
    }

    .box_danger .yd-accordion-title {
        color: #000;
    }

    .box_danger .yd-accordion {
        margin-top: .2rem;
    }
</style>



